import Logo from '../childComp/Logo.vue';
import wInput from 'components/watch-login/watch-input.vue';
import wButton from 'components/watch-login/watch-button.vue';

import {expTest} from 'common/utils.js'
import {login} from 'network/user.js'

export default {
	data() {
		return {
			phone: '', //用户/电话
			password: '', //密码
			isRotate: false, //是否加载旋转
		};
	},
	components: {
		Logo,
		wInput,
		wButton,
	},
	mounted() {
		_this = this;
		//this.isLogin();
	},
	methods: {

		startLogin() {
			//登录
			if (this.isRotate) {
				//判断是否加载中，避免重复点击请求
				return false;
			}

			if (!expTest('phone', this.phone)) {
				uni.showToast({
					icon: 'none',
					position: 'bottom',
					title: '请填入正确的手机号码'
				});
				return;
			}
			if (!this.password) {
				uni.showToast({
					icon: 'none',
					position: 'bottom',
					title: '请输入密码'
				});
				return;
			}

			console.log("测试")

			this.isRotate = true

			uni.showLoading({
				title: '登录中'
			});

			login({
				phone: this.phone,
				password: this.password
			}).then((success) => {
				console.log('请求成功')
				console.log(success)
				if (success.data.err_code !== 0) {
					this.isRotate = false
					uni.hideLoading();
					uni.showToast({
						icon: 'error',
						position: 'center',
						title: success.data.message
					});
					return false
				}
				
				uni.setStorageSync('TOKEN', success.data.data.token)
				uni.hideLoading();
				uni.showToast({
					duration: 2000,
					icon: 'success',
					position: 'center',
					title: "登录成功",
					mask: true,
					complete: () => {
						uni.redirectTo({
							url: '/pages/index/index'
						})
					}
				});

			}, (error) => {
				console.log(error)
				this.isRotate = false
				uni.hideLoading();
				uni.showToast({
					icon: 'error',
					position: 'center',
					title: success.data.message
				});
			})
		}
	}
}
